"use client";
import React, { useState } from "react";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "../ui/table";
import Badge from "../ui/badge/Badge";
import { Modal } from "../ui/modal";
import jsPDF from "jspdf";

interface Payslip {
    id: number;
    payPeriod: string;
    grossPay: number;
    deductions: number;
    netPay: number;
    status: "Paid" | "Pending";
    employeeName: string;
    employeeId: string;
    department: string;
    earnings: { label: string; amount: number }[];
    deductionsDetails: { label: string; amount: number }[];
}

const payslipsData: Payslip[] = [
    {
        id: 1,
        payPeriod: "October 2025",
        grossPay: 6500,
        deductions: 1430,
        netPay: 5070,
        status: "Paid",
        employeeName: "John Doe",
        employeeId: "EMP-2024-001",
        department: "Engineering",
        earnings: [
            { label: "Base Salary", amount: 6000 },
            { label: "Performance Bonus", amount: 500 },
        ],
        deductionsDetails: [
            { label: "Federal Tax", amount: 980 },
            { label: "Social Security", amount: 403 },
            { label: "Health Insurance", amount: 47 },
        ],
    },
    // Add more payslips if needed
];

export default function PayslipsPage() {
    const [selectedPayslip, setSelectedPayslip] = useState<Payslip | null>(null);

    const handleView = (payslip: Payslip) => setSelectedPayslip(payslip);
    const handleClose = () => setSelectedPayslip(null);

    const handleDownloadPDF = () => {
        if (!selectedPayslip) return;

        const doc = new jsPDF();
        doc.setFontSize(16);
        doc.text("Payslip", 105, 15, { align: "center" });

        doc.setFontSize(12);
        doc.text(`Employee Name: ${selectedPayslip.employeeName}`, 20, 30);
        doc.text(`Employee ID: ${selectedPayslip.employeeId}`, 20, 37);
        doc.text(`Department: ${selectedPayslip.department}`, 20, 44);
        doc.text(`Pay Period: ${selectedPayslip.payPeriod}`, 20, 51);

        let y = 60;
        doc.text("Earnings:", 20, y);
        selectedPayslip.earnings.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 30, y);
        });
        y += 7;
        doc.text(`Gross Pay: $${selectedPayslip.grossPay.toFixed(2)}`, 30, y);

        y += 10;
        doc.text("Deductions:", 20, y);
        selectedPayslip.deductionsDetails.forEach((item) => {
            y += 7;
            doc.text(`${item.label}: $${item.amount.toFixed(2)}`, 30, y);
        });
        y += 7;
        doc.text(`Total Deductions: $${selectedPayslip.deductions.toFixed(2)}`, 30, y);

        y += 10;
        doc.text(`Net Pay: $${selectedPayslip.netPay.toFixed(2)}`, 20, y);

        doc.save(`Payslip_${selectedPayslip.payPeriod}.pdf`);
    };

    return (
        <div className="overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-white/[0.05] dark:bg-white/[0.03] p-6">
            <h1 className="text-2xl font-bold mb-6">Payslips</h1>

            <div className="max-w-full overflow-x-auto">
                <div className="min-w-[1102px]">
                    <Table>
                        <TableHeader className="border-b border-gray-100 dark:border-white/[0.05]">
                            <TableRow>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Pay Period
                                </TableCell>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Gross Pay
                                </TableCell>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Deductions
                                </TableCell>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Net Pay
                                </TableCell>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Status
                                </TableCell>
                                <TableCell isHeader className="px-5 py-3 font-medium text-gray-500 text-start text-theme-xs dark:text-gray-400">
                                    Action
                                </TableCell>
                            </TableRow>
                        </TableHeader>

                        <TableBody className="divide-y divide-gray-100 dark:divide-white/[0.05]">
                            {payslipsData.map((p) => (
                                <TableRow key={p.id}>
                                    <TableCell className="px-5 py-4 sm:px-6 text-start">{p.payPeriod}</TableCell>
                                    <TableCell className="px-4 py-3 text-gray-500 text-start text-theme-sm dark:text-gray-400">${p.grossPay.toFixed(2)}</TableCell>
                                    <TableCell className="px-4 py-3 text-gray-500 text-start text-theme-sm dark:text-gray-400">${p.deductions.toFixed(2)}</TableCell>
                                    <TableCell className="px-4 py-3 text-gray-500 text-start text-theme-sm dark:text-gray-400">${p.netPay.toFixed(2)}</TableCell>
                                    <TableCell className="px-4 py-3 text-gray-500 text-start text-theme-sm dark:text-gray-400">
                                        <Badge size="sm" color={p.status === "Paid" ? "success" : "warning"}>
                                            {p.status}
                                        </Badge>
                                    </TableCell>
                                    <TableCell className="px-4 py-3 text-gray-500 text-theme-sm dark:text-gray-400">
                                        <button
                                            onClick={() => handleView(p)}
                                            className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
                                        >
                                            View
                                        </button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </div>
            </div>

            {/* Modal for Payslip Details */}
            <Modal isOpen={!!selectedPayslip} onClose={handleClose} className="max-w-2xl p-6">
                {selectedPayslip && (
                    <div className="space-y-4">
                        <h2 className="text-xl font-bold">Payslip Details</h2>
                        <p><strong>Name:</strong> {selectedPayslip.employeeName}</p>
                        <p><strong>Employee ID:</strong> {selectedPayslip.employeeId}</p>
                        <p><strong>Department:</strong> {selectedPayslip.department}</p>
                        <p><strong>Pay Period:</strong> {selectedPayslip.payPeriod}</p>

                        <div>
                            <h3 className="font-semibold">Earnings</h3>
                            <ul>
                                {selectedPayslip.earnings.map((e) => (
                                    <li key={e.label}>{e.label}: ${e.amount.toFixed(2)}</li>
                                ))}
                            </ul>
                            <p className="font-bold">Gross Pay: ${selectedPayslip.grossPay.toFixed(2)}</p>
                        </div>

                        <div>
                            <h3 className="font-semibold">Deductions</h3>
                            <ul>
                                {selectedPayslip.deductionsDetails.map((d) => (
                                    <li key={d.label}>{d.label}: ${d.amount.toFixed(2)}</li>
                                ))}
                            </ul>
                            <p className="font-bold">Total Deductions: ${selectedPayslip.deductions.toFixed(2)}</p>
                        </div>

                        <p className="font-bold text-lg">Net Pay: ${selectedPayslip.netPay.toFixed(2)}</p>

                        <div className="flex justify-end">
                            <button
                                onClick={handleDownloadPDF}
                                className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                            >
                                Download PDF
                            </button>
                        </div>
                    </div>
                )}
            </Modal>
        </div>
    );
}
